<template>
    <div class="body-bg">
        <div class="formContainer">
            <el-form ref="loginFormRef" :model="loginForm" status-icon :rules="loginRules" label-width="120px"
                class="demo-ruleForm">
                <el-form-item label="user name" prop="username">
                    <el-input v-model="loginForm.username" autocomplete="off" />
                </el-form-item>
                <el-form-item label="password" prop="password">
                    <el-input v-model="loginForm.password" type="password" autocomplete="off" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm(ruleFormRef)">
                        Login
                    </el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script setup>
import { reactive } from 'vue'
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import axios from 'axios'
import { ElMessage } from 'element-plus'

// 表单绑定的响应式对象
const loginForm = reactive({
    username: "",
    password: ""
})

// 表单的引用对象
const loginFormRef = ref()

// 一些用户名和密码的规则
const loginRules = reactive({
    username: [
        {
            // 当失去焦点时，检查是否为空
            required: true, message: "Please enter your username", trigger: "blur"
        }
    ],
    password: [
        {
            required: true, message: "Please enter your password", trigger: "blur"
        }
    ]
});

const router = useRouter()

// 按下Login后提交表单
const submitForm = () => {
    // 点击登录时检查是否为空
    loginFormRef.value.validate((valid) => {
        // 非空，提交
        if (valid) {
            // 获取username和password提交
            console.log(loginForm)


            axios.post("/adminapi/user/login", loginForm).then(res => {
                console.log(res.data)
                if (res.data.ActionType === "OK") {
                    // 赋值token
                    localStorage.setItem("token", "LOGIN_SUCCESS")
                    router.push("/habitai")
                }
                else {
                    ElMessage.error("Incorrect username or password!")
                }
            })

        }
    })
}

</script>

<style lang="scss" scoped>

.body-bg {
    position: relative;
        width: 100vw;
    padding: 0;
    margin: 0;
    height: 100vh;
    font-size: 16px;
    background-position: left top;
    background-color: #31746b;
    color: #301c1c;
    font-family: "Source Sans Pro";
    position: relative;
}

.body-bg::before {
    content: "Welcome to habitAI\A (^_^)";
    white-space: pre; /* 保留空白符 */
    text-align: center; /* 水平居中 */
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white; /* 文字颜色 */
    font-size: 4em;
    font-weight: bold;
}
.formContainer {
    width: 500px;
    height: 300px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: rgba($color: #292626, $alpha: 0.2);
    color: white;
    text-align: center;
    padding: 20px;
    border-radius: 15px;

}

::v-deep .el-form-item__label {
    color: white;
}
</style>