import { createStore } from 'vuex'

export default createStore({
  state: {
    is_get_router: false, // 用于指示是否为第一次转页面
    is_collapsed: true, // 侧边是否折叠
  },
  getters: {
  },
  mutations: {
    changeGetRouter(state, value){
      state.is_get_router = value
    },
    changeCollapsed(state){
      state.is_collapsed = !state.is_collapsed
    }
  },
  actions: {
  },
  modules: {
  }
})
