import Home from '@/views/home/Home.vue'
import Center from '@/views/center/Center.vue'


const routes = [
    {
        path: "/habitai",  //  /: from the root directory
        component: Home,
    },
    {
        path: "/center",
        component: Center,
    },
    {
        path: "/",
        redirect: "/habitai",
    },
    
]

export default routes
